
.footer {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: #f3f4f6;
  text-align: center;
  padding: 10px 0;
  font-size: 0.85rem;
  box-shadow: 0 -2px 8px rgba(102, 126, 234, 0.3);
  width: 100%;
}

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
}

.footer-links a {
  margin: 0 10px;
  color: #f3f4f6;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

.footer-links a:hover {
  color: #ffffff;
}

.footer p {
  margin: 0;
  letter-spacing: 0.4px;
} 
